<template>
  <div class="myData">
    <div class="title">
      <span class="vertical"></span>
      <span class="titleText unSelect">我的数据源</span>
      <span class="btn mainBtn"
        v-show="addShow">
        <i class="icon biFont icon-add addData unSelect"></i>
        <span class="unSelect">添加数据</span>
      </span>
    </div>
    <DataSource @myDataBack="dataListEvent"
      @chosenDataBack="chosenDataEvent"
      :dataTitle="dataTitle"
      :requestUrl="requestUrl"
      class="data-source"></DataSource>
    <DataShow v-bind="{dataList,chosen}"
      class="data-table"
      v-if="dataTable"></DataShow>
  </div>
</template>

<script>
import DataShow from '@/pages/MyDataSource/components/DataSourceTable.vue';
import DataSource from '@/pages/DataSource/index.vue';

export default {
	name: 'myDataSource',
	components: { DataSource, DataShow },
	data() {
		return {
			dataList: {
				id: null
			},
			chosen: null,
			myDataBack: '',
			addShow: false,
			dataTable: false,
			requestUrl: 'api/dataInfo',
			dataTitle: {
				title: '数据源',
				icon: 'dataBase'
			}
		};
	},
	mounted() {},
	methods: {
		dataListEvent(data) {
			if (data.id != this.dataList.id) {
				this.dataTable = false;
				this.$nextTick(() => {
					this.dataTable = true;
				});
				this.dataList = data;
			}
		},

		chosenDataEvent(data) {
			this.chosen = data.field;
			this.dataListEvent(data.data);
		}
	}
};
</script>
<style scoped>
.myData .title {
  color: #fff;
  position: absolute;
  padding: 3.9vh 0 0 5.1vw;
}

.myData .title .titleText {
  font-size: 0.9vw;
}

.myData .title .vertical {
  width: 0.1vw;
  height: 1.9vh;
  margin-right: 0.3vw;
  display: inline-block;
  vertical-align: -0.2vh;
  background-color: #28b3fc;
}

.myData .title .btn {
  font-size: 0.8vw;
  padding: 0.6vh 1vw;
  margin-left: 1.9vw;
}

.myData .title .btn .addData {
  font-size: 0.6vw;
  vertical-align: 0.15em;
}
</style>

