<template>
  <div class='dataTable'
    v-if='dataShow'>
    <div class='table'>
      <hot-table :settings='setHotTable'
        licenseKey='non-commercial-and-evaluation'
        ref='testHot'></hot-table>
    </div>
<!--    <Tips v-bind='{tipsData}'-->
<!--      ref='tips'></Tips>-->
  </div>
</template>

<script>
// import Tips from '@/components/Tips/Tips.vue';
import {HotTable} from '@handsontable/vue';
import 'handsontable/dist/handsontable.css';
import 'handsontable/languages/zh-CN';

export default {
	name: 'dataSourceTable',
	components: {
		HotTable,
		// Tips
	},
	props: ['dataList', 'chosen'],
	data() {
		return {
			userid: localStorage.getItem('userid'),
			token: localStorage.getItem('token'),
			field: null,
			addShow: false,
			dataShow: false,
			totalPage: null,
			handsonTable: null,
			HotTableTh: [],
			page: 1,
			limit: 30,
			tableObj: [],
			tipsData: {
				text: '',
				rel: '',
				time: 1500
			},
			scrollOpt: {
				barColor: '#95A0B6', //滚动条颜色
				barWidth: 6, //滚动条宽度
				railColor: '#475566', //导轨颜色
				barMarginRight: 0, //垂直滚动条距离整个容器右侧距离单位（px）
				barOpacityMin: 0.3, //滚动条非激活状态下的透明度
			},

			setHotTable: {
				data: [],
				width: '77.1vw',
				height: '83.3vh',
				rowHeaders: false, // 行表头
				colHeaders: [], // 自定义列表头or 布尔值
				readOnly: true,
				stretchH: 'all', //根据宽度横向扩展，last:只扩展最后一列，none：默认不扩展
				className: 'htLeft',
				currentColClassName: 'currentCol', // 为选中列添加类名
				manualColumnResize: true, //当值为true时，允许拖动，当为false时禁止拖动
				autoWrapRow: true, // 自动换行
				language: 'zh-CN',
				fillHandle: false,
				afterScrollVertically: () => {
					let targetElement = this.$refs.testHot.$el.children[0].children[0];
					let insideHeight = targetElement.children[0].offsetHeight;
					let rollingHeight = insideHeight - targetElement.offsetHeight;
					if (rollingHeight <= targetElement.scrollTop + 5) {
						this.page++;
						if (this.page <= this.totalPage) {
							this.requestData(this.page);
						}
					}
				}
			}
		};
	},
	methods: {
		requestData(page) {
			let self = this;
			return self
				.$get('api/tableInfo', {
					source_sn: self.dataList.source_sn,
					id: self.dataList.id,
					page: page,
					limit: 30
				})
				.then(
					response => {
						let res = JSON.parse(JSON.stringify(response.data));
						let emptyData;
						if (response.code == '000000') {
							self.dataShow = true;
							if (page == 1) {
								if (res.tableinfo.length === 0) {
									let emptyHead = new Array(res.head.length).fill('');
									emptyData = new Array(26).fill(emptyHead);
									res.tableinfo = emptyData;
								} else if (res.tableinfo.length < 30) {
									self.setHotTable.minSpareRows = 26 - res.tableinfo.length;
								}
								self.setHotTable.data = res.tableinfo;
								self.setHotTable.colHeaders = res.head;
								Promise.resolve();
							} else {
								self.setHotTable.data.push(...res.tableinfo);
								// }
							}
							self.totalPage = res.total_page;
						} else {
							this.tips.show([response.err_msg.split(':')[1]]);
							// self.tipsData.rel = 'fail';
							// self.tipsData.text = response.err_msg.split(':')[1];
							// self.$refs.tips.closeTips();
						}
					},
					error => {
						console.log(error);
					}
				);
		}
	},
	watch: {
		chosen: {
			handler(val, old) {
				let col = this.handsonTable.propToCol(val.Field);
				this.handsonTable.selectColumns(col);
			}
		},
		dataList: {
			handler(val, old) {
				this.requestData(1);
				this.setHotTable.data = null;
				this.setHotTable.colHeaders = null;
				this.heads = null;
			}
		}
	},
	mounted() {
		this.requestData(1).then(() => {
			this.$nextTick(()=>{
				this.handsonTable = this.$refs.testHot.hotInstance;
			});
		});
	}
};
</script>

<style scoped>
  .dataTable {
    width: 84.6%;
    height: 100%;
    color: #fff;
    padding: 3.9vh 1.7vw 2.4vh 5.1vw;
  }

  .dataTable .table {
    margin-top: 4.5vh;
    width: 77.1vw;
    height: 83.3vh;
    overflow: hidden;
  }

  .dataTable .table >>> .handsontable table,
  .dataTable .table >>> .handsontable tbody,
  .dataTable .table >>> .handsontable thead,
  .dataTable .table >>> .handsontable td,
  .dataTable .table >>> .handsontable th,
  .dataTable .table >>> .handsontable input,
  .dataTable .table >>> .handsontable textarea,
  .dataTable .table >>> .handsontable div {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

  .dataTable .table >>> .handsontable td.area:before,
  .dataTable .table >>> .htBorders,
  .dataTable .table >>> #hot-display-license-info {
    overflow: hidden;
    opacity: 0;
  }

  .dataTable .table >>> .ht_clone_top,
  .dataTable .table >>> .ht_clone_top .wtHolder {
    width: 76.85vw !important;
  }

  .dataTable .table >>> .handsontable th {
    font-size: 0.7vw;
    background-color: #273045;
    border-left: solid 0.0625vw #475566;
    border-top: solid 0.0625vw #475566;
    border-right: solid 0.0625vw transparent;
    border-bottom: solid 0.0625vw #475566;
    height: 3.1vh !important;
    line-height: 3.1vh;
    cursor: pointer;
    text-align: left;
    white-space: nowrap;
    color: #ffffff;
    padding: 0 0.3vw;
    border-left-width: 0.0625vw !important;
  }

  .dataTable .table >>> .handsontable thead th.ht__active_highlight {
    background-color: #184562;
    border: solid 0.0625vw #28b3fc;
  }

  .dataTable .table >>> .handsontable tr {
    background: none;
  }

  .dataTable .table >>> .handsontable th:hover {
    background-color: #184562;
    border: solid 0.0625vw #28b3fc;
  }

  .dataTable .table >>> .handsontable td:hover {
    background-color: #1f2838;
  }

  .dataTable .table >>> .handsontable td {
    background-color: #161d29;
    height: 3.1vh !important;
    line-height: 3.1vh;
    border-left: solid 0.0625vw #475566;
    border-bottom: solid 0.0625vw #475566;
    border-right: solid 0.0625vw transparent;
    border-top: solid 0.0625vw transparent;
    white-space: nowrap;
    padding: 0 0.3vw;
    color: #d0d0d8;
  }

  .dataTable .table >>> td.htDimmed.area.highlight {
    background-color: #1f2838;
  }

  .dataTable .table >>> .handsontable th:last-child {
    border-bottom: solid 0.0625vw #475566;
    border-right: solid 0.0625vw #475566;
  }

  .dataTable .table >>> .handsontable td:last-child {
    border-right: solid 0.0625vw #475566;
  }

  .dataTable .table >>> .handsontable th,
  .dataTable .table >>> .handsontable td {
    background-clip: border-box;
  }

  .dataTable .table >>> .handsontable thead tr:last-child th {
    border-bottom-width: 0.0625vw;
  }

  .dataTable .table >>> .handsontable .manualColumnResizer {
    width: 0.1vw;
  }

  .dataTable .table >>> .handsontable .manualColumnResizerGuide {
    background-color: #28b3fc;
    width: 0.1vw;
    border-right: 0.0625vw dashed #777;
  }

  .dataTable .table >>> .handsontable .ht_master .wtHolder::-webkit-scrollbar {
    /* 滚动条的宽度 */
    width: 0.3vw;
    height: 0.3vw;
  }

  .dataTable .table >>> .handsontable .wtHolder::-webkit-scrollbar-track-piece {
    /* 滚动条凹槽的颜色，还可以设置边框属性 */
    background-color: #475566;
    border-radius: 0.2vw;
  }

  .dataTable .table >>> .handsontable .wtHolder::-webkit-scrollbar-thumb {
    /* 滚动条的设置 */
    border-radius: 0.2vw;
    background-color: #95a0b6;
    background-clip: padding-box;
  }

  .dataTable .table >>> .handsontable .wtHolder::-webkit-scrollbar-thumb:hover {
    background-color: #95a0b6;
  }
</style>
